import React from 'react';
import { FC  } from 'react';
import './MyButton.css'

export interface MyButtonProps {
    // primary: boolean,
    // red: boolean,
    // yellow: boolean,
    // green: boolean,
    // violet: boolean
    label: string,
    disabled?: boolean,
    outline?: boolean,
    size?: 'small' | 'medium' | 'large';
    variant: 'primary' | 'red' | 'yellow' | 'green'
    onClick?: () => void;
}
const MyButton: FC<MyButtonProps> =
    ({
         variant,
         disabled,
         ...props
    }) => {

    const rootClasses = ['my-button primary']

        if (variant === 'primary') {
            rootClasses.push('primary')
        } else if (variant === 'red') {
            rootClasses.push('red')
        } else if (variant === 'yellow') {
            rootClasses.push('yellow')
        } else if (variant === 'green') {
            rootClasses.push('green')
        } else if (variant === 'violet') {
            rootClasses.push('violet')
        }

        if (disabled) {
            rootClasses.push('disabled')
        }

    return (
        <button {...props} className={rootClasses.join(' ')}>
            { props.label }
        </button>
    )
}

export default MyButton
